$primary-color: #252b40;
$def-background-color: #ffffff;
$main-text-color: #ffffff;
$link-active-color: #fec017;

$card-drop-shadow: drop-shadow(0px 1px 2px rgba(0, 0, 0, 0.25));

$desktop-max-width: 1440px;

$breakpoint-desktop: 1200px;
$breakpoint-tablet: 768px;
$breakpoint-mobile: 450px;

@mixin container-width {
}
@mixin content-width {
	max-width: $desktop-max-width;
	margin: auto;
}
@mixin standard-font($fontSize: 400) {
	font-family: 'Segoe UI', sans-serif;
	font-style: normal;
	font-weight: $fontSize;
}
@mixin h1-size-height {
	font-size: 40px;
	line-height: 48px;
}
@mixin h2-size-height {
	font-size: 32px;
	line-height: 40px;
}
@mixin p-size-height {
	font-size: 16px;
	line-height: 24px;
}
@mixin link-size-height {
	font-size: 20px;
	line-height: 24px;
}
